.rich-text {
	padding: 10px 20px 60px;
	background-color: #fff;
}

.h5-html {
  display: block;
}
.h5-head {
  display: none;
}
.h5-meta {
  display: none;
}
.h5-title {
  display: none;
}
.h5-link {
  display: none;
}
.h5-style {
  display: none;
}
.h5-script {
  display: none;
}
.h5-body {
  display: block;
  margin: 8px;
}
.h5-p {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0;
  margin-inline-end: 0;
}
.h5-div {
  display: block;
}
.h5-layer {
  display: block;
}
.h5-article,
.h5-aside,
.h5-footer,
.h5-header,
.h5-hgroup,
.h5-main,
.h5-nav,
.h5-section {
  display: block;
}
.h5-marquee {
  display: inline-block;
  width: -webkit-fill-available;
}
.h5-address {
  display: block;
}
.h5-blockquote {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 40px;
  margin-inline-end: 40px;
}
.h5-figcaption {
  display: block;
}
.h5-figure {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 40px;
  margin-inline-end: 40px;
}
.h5-q {
  display: inline;
}
.h5-q:before {
  content: open-quote;
}
.h5-q:after {
  content: close-quote;
}
.h5-center {
  display: block;
  text-align: -webkit-center;
}
.h5-hr {
  display: block;
  overflow: hidden;
  unicode-bidi: isolate;
  margin-block-start: 0.5em;
  margin-block-end: 0.5em;
  margin-inline-start: auto;
  margin-inline-end: auto;
  border-style: inset;
  border-width: 1px;
}
.h5-map {
  display: inline;
}
.h5-video {
  object-fit: contain;
}
.h5-h1 {
  display: block;
  font-size: 2em;
  margin-block-start: 0.67em;
  margin-block-end: 0.67em;
  margin-inline-start: 0;
  margin-inline-end: 0;
  font-weight: bold;
}
