.h5-h2 {
  display: block;
  font-size: 1.5em;
  margin-block-start: 0.83em;
  margin-block-end: 0.83em;
  margin-inline-start: 0;
  margin-inline-end: 0;
  font-weight: bold;
}
.h5-h3 {
  display: block;
  font-size: 1.17em;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0;
  margin-inline-end: 0;
  font-weight: bold;
}
.h5-h4 {
  display: block;
  margin-block-start: 1.33em;
  margin-block-end: 1.33em;
  margin-inline-start: 0;
  margin-inline-end: 0;
  font-weight: bold;
}
.h5-h5 {
  display: block;
  font-size: 0.83em;
  margin-block-start: 1.67em;
  margin-block-end: 1.67em;
  margin-inline-start: 0;
  margin-inline-end: 0;
  font-weight: bold;
}
.h5-h6 {
  display: block;
  font-size: 0.67em;
  margin-block-start: 2.33em;
  margin-block-end: 2.33em;
  margin-inline-start: 0;
  margin-inline-end: 0;
  font-weight: bold;
}
.h5-table {
  display: table;
  border-collapse: separate;
  border-spacing: 2px;
  border-color: gray;
  box-sizing: border-box;
  text-indent: initial;
}
.h5-thead {
  display: table-header-group;
  vertical-align: middle;
  border-color: inherit;
}
.h5-tbody {
  display: table-row-group;
  vertical-align: middle;
  border-color: inherit;
}
.h5-tfoot {
  display: table-footer-group;
  vertical-align: middle;
  border-color: inherit;
}
.h5-table > .h5-tr {
  vertical-align: middle;
}
.h5-col {
  display: table-column;
}
.h5-colgroup {
  display: table-column-group;
}
.h5-tr {
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
}
.h5-td,
.h5-th {
  display: table-cell;
  vertical-align: inherit;
}
.h5-th {
  font-weight: bold;
  text-align: -internal-center;
}
.h5-caption {
  display: table-caption;
  text-align: -webkit-center;
}
.h5-ul,
.h5-menu,
.h5-dir {
  display: block;
  list-style-type: disc;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0;
  margin-inline-end: 0;
  padding-inline-start: 40px;
}
.h5-ol {
  display: block;
  list-style-type: decimal;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0;
  margin-inline-end: 0;
  padding-inline-start: 40px;
}
.h5-li {
  display: list-item;
  text-align: -webkit-match-parent;
}
.h5-ul .h5-ul,
.h5-ol .h5-ul {
  list-style-type: circle;
}
.h5-ol .h5-ol .h5-ul,
.h5-ol .h5-ul .h5-ul,
.h5-ul .h5-ol .h5-ul,
.h5-ul .h5-ul .h5-ul {
  list-style-type: square;
}
.h5-dd {
  display: block;
  margin-inline-start: 40px;
}
.h5-dl {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0;
  margin-inline-end: 0;
}
.h5-dt {
  display: block;
}
.h5-ol .h5-ul,
.h5-ul .h5-ol,
.h5-ul .h5-ul,
.h5-ol .h5-ol {
  margin-block-start: 0;
  margin-block-end: 0;
}
.h5-form {
  display: block;
  margin-top: 0em;
}
.h5-label {
  cursor: default;
}
.h5-legend {
  display: block;
  padding-inline-start: 2px;
  padding-inline-end: 2px;
  border: none;
}
.h5-fieldset {
  display: block;
  margin-inline-start: 2px;
  margin-inline-end: 2px;
  padding-block-start: 0.35em;
  padding-inline-start: 0.75em;
  padding-inline-end: 0.75em;
  padding-block-end: 0.625em;
  border: 2px groove ThreeDFace;
  min-inline-size: min-content;
}
.h5-button {
  appearance: auto;
}
.h5-input,
.h5-textarea,
.h5-select,
.h5-button,
.h5-meter,
.h5-progress {
  -webkit-writing-mode: horizontal-tb !important;
}
.h5-input,
.h5-textarea,
.h5-select,
.h5-button {
  margin: 0em;
  font: -webkit-small-control;
  text-rendering: auto;
  color: black;
  letter-spacing: normal;
  word-spacing: normal;
  line-height: normal;
  text-transform: none;
  text-indent: 0;
  text-shadow: none;
  display: inline-block;
  text-align: start;
}
.h5-textarea {
  appearance: auto;
  border: 1px solid #767676;
  column-count: initial !important;
  -webkit-rtl-ordering: logical;
  resize: auto;
  cursor: text;
  padding: 2px;
  white-space: pre-wrap;
  word-wrap: break-word;
  background-color: #ffffff;
  font-family: monospace;
}
.h5-input {
  appearance: auto;
  padding: 1px 0;
  border: 2px inset #767676;
  -webkit-rtl-ordering: logical;
  cursor: text;
  background-color: #ffffff;
}
.h5-option:-internal-spatial-navigation-focus {
  outline: black dashed 1px;
  outline-offset: -1px;
}
.h5-datalist {
  display: none;
}
.h5-area {
  display: inline;
}
.h5-param {
  display: none;
}
.h5-select {
  appearance: auto;
  box-sizing: border-box;
  align-items: center;
  white-space: pre;
  -webkit-rtl-ordering: logical;
  color: black;
  background-color: #ffffff;
  border: 1px solid #767676;
  cursor: default;
  border-radius: 0;
}
.h5-optgroup {
  font-weight: bolder;
  display: block;
}
.h5-option {
  font-weight: normal;
  display: block;
  padding: 0 2px 1px 2px;
  white-space: nowrap;
  min-height: 1.2em;
}
.h5-selectmenu {
  display: inline-block;
}
.h5-output {
  display: inline;
}
.h5-meter {
  appearance: auto;
  box-sizing: border-box;
  display: inline-block;
  height: 1em;
  width: 5em;
  vertical-align: -0.2em;
  -webkit-user-modify: read-only !important;
}
.h5-progress {
  appearance: auto;
  box-sizing: border-box;
  display: inline-block;
  height: 1em;
  width: 10em;
  vertical-align: -0.2em;
}
.h5-u,
.h5-ins {
  text-decoration: underline;
}
.h5-strong,
.h5-b {
  font-weight: bold;
}
.h5-i,
.h5-cite,
.h5-em,
.h5-var,
.h5-address,
.h5-dfn {
  font-style: italic;
}
.h5-tt,
.h5-code,
.h5-kbd,
.h5-samp {
  font-family: monospace;
}
.h5-pre,
.h5-xmp,
.h5-plaintext,
.h5-listing {
  display: block;
  font-family: monospace;
  white-space: pre;
  margin: 1em 0;
}
.h5-mark {
  background-color: yellow;
  color: black;
}
.h5-big {
  font-size: larger;
}
.h5-small {
  font-size: smaller;
}
.h5-s,
.h5-strike,
.h5-del {
  text-decoration: line-through;
}
.h5-sub {
  vertical-align: sub;
  font-size: smaller;
}
.h5-sup {
  vertical-align: super;
  font-size: smaller;
}
.h5-nobr {
  white-space: nowrap;
}
.h5-ruby,
.h5-rt {
  text-indent: 0;
}
.h5-rt {
  line-height: normal;
  -webkit-text-emphasis: none;
}
.h5-ruby > .h5-rt {
  display: block;
  font-size: 50%;
  text-align: start;
}
.h5-rp {
  display: none;
}
.h5-noframes {
  display: none;
}
.h5-frameset,
.h5-frame {
  display: block;
}
.h5-frameset {
  border-color: inherit;
}
.h5-iframe {
  border: 2px inset;
}
.h5-details {
  display: block;
}
.h5-summary {
  display: block;
}
.h5-template {
  display: none;
}
.h5-bdi,
.h5-output {
  unicode-bidi: isolate;
}
.h5-bdo {
  unicode-bidi: bidi-override;
}
.h5-dialog:not([open]) {
  display: none;
}
.h5-dialog {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  width: fit-content;
  height: fit-content;
  margin: auto;
  border: solid;
  padding: 1em;
  background: white;
  color: black;
}
.h5-dialog::backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.1);
}
.h5-slot {
  display: contents;
}
